html {
  scroll-behavior: smooth;
}
.top-menu {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 104px;
  width: 100vw;
}
.top-menu > span {
  position: relative;
  margin-left: 54px;
  font-size: small;
  cursor: pointer;
  font-weight: bold;
}
.top-menu > img {
  position: relative;
  width: 104px;
  height: 104px;
  margin-left: 50px;
}
.beauty,
.fitness {
  position: relative;
  height: 590px;
  width: 100vw;
  background-image: url(../pictures/keira-knightley.png);
  background-repeat: no-repeat;
  background-size: 950px;
  box-sizing: border-box;
  background-color: black;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
  animation: mymove 3s;
  @keyframes mymove {
    from {
      background-size: 965px;
    }
    to {
      background-size: 950px;
    }
  }
}
.fitness {
  animation: my-move 3s;

  background-image: url(../pictures/ballerina-3055155.jpg);
  background-size: 1600px;
  @keyframes my-move {
    from {
      background-size: 1535px;
    }
    to {
      background-size: 1600px;
    }
  }
}

.test {
  height: 590px;
  width: 680px;
  position: relative;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}
body {
  overflow-y: scroll;
  height: 100vh;
  position: relative;
  overflow-x: hidden;
}

.insider {
  width: 100vw;
  height: fit-content;
  margin-top: -200px;
  background-color: transparent;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: auto;
  padding-bottom: 40px;
  margin-bottom: 50px;
}

.insider::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  padding-top: 40px;
}

.insider::-webkit-scrollbar-track {
  background: snow;
  border-radius: 10px;
  margin-left: 513px;
}
.insider::-webkit-scrollbar-thumb {
  border-radius: 10px;
  height: 10px;
  width: 8px;
  border: none;
  background: rgb(116, 103, 103);
  background: -webkit-linear-gradient(rgb(105, 70, 70), rgb(65, 55, 55));
}

.product-container {
  width: 220px;
  box-sizing: border-box;
  height: 470px;
  margin-left: 50px;

  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 15px;
}
.product-container:hover {
  background-color: white;
  position: relative;
  box-shadow: 0px 3px 16px 2px rgba(128, 128, 128, 0.322);
  transition: width 0.5s 0.5s, height 0.5s 0.5s, background-color 0.5s;
}

.product-description {
  margin-top: 10px;
}
.product1,
.product2,
.product3,
.product4 {
  height: 250px;
  width: 50%;
  background-size: 120%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  align-items: center;
}
.product2 {
  background-image: url("../pictures/p2.png");
}
.product1 {
  background-image: url("../pictures/p1.png");
}
.product3 {
  background-image: url("../pictures/p3.png");
}
.product4 {
  background-image: url("../pictures/p4.png");
}
.dance1,
.dance2,
.dance3,
.dance4,
.dance5 {
  height: 250px;
  width: 60%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  align-items: center;
}
.dance1 {
  background-image: url("../pictures/jazz1.png");
  background-size: 96%;
}
.dance2 {
  background-image: url("../pictures/tango1.png");

  background-size: 141%;
}
.dance3 {
  background-image: url("../pictures/ballet1.png");
}
.dance4 {
  background-image: url("../pictures/zumba1.png");
  background-size: 107%;
}
.fade-in {
  flex-shrink: 0;
  width: 40vw;
  padding-left: 150px;
  box-sizing: border-box;
  padding-top: 170px;
  font-size: 40px;
  opacity: 1;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}
.fade-out {
  opacity: 0;
  flex-shrink: 0;
  width: 40vw;
  padding-left: 150px;
  box-sizing: border-box;
  padding-top: 170px;
  font-size: 40px;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}
.lazy {
  background-color: chartreuse;
  height: 590px;
  width: 680px;
  // transform: scale(1.1);

  // transition: transform 3s;
}
// //////////////////////////////////////////////////////////
.overlay {
  position: fixed;
  display: block;
  overflow: auto;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background-color: transparent;
  z-index: 999;
}

.content {
  background-color: yellow;
  width: 95vw;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem;
  position: relative;
  overflow: auto;
  align-self: auto;
  margin-top: 96px;
}
.footer {
  height: fit-content;
  width: 100vw;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.title-container {
  display: flex;
  height: fit-content;
  padding-top: 60px;
  padding-bottom: 60px;
  justify-content: flex-start;
  box-sizing: border-box;
  align-items: flex-start;
  flex-direction: column;
  color: snow;
  margin-left: 60px;
}
.titles {
  font-size: 20px;
  margin-bottom: 35px;
}
.subtitles {
  font-size: 15px;
  margin-bottom: 10px;
}
.empty {
  width: 70px;
  box-sizing: border-box;
  height: 470px;

  background-color: transparent;
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 15px;
}
.grid{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: blue;
  
}